@import url(https://fonts.googleapis.com/css?family=Nunito:300,300i,400,400i,600,700,800,900);
.container{position:relative;margin-top:100px}.container img{display:block;width:100%}nav{position:fixed;z-index:10;left:0;right:0;top:0;padding:0 5%;height:4rem;background-color:#FFF}nav .logo{float:left;width:40%;height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;font-size:24px;color:#fff}nav .links{float:right;padding:0;margin:0;width:60%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}nav .links li{list-style:none}nav .links a{display:block;padding:1em;font-size:16px;font-weight:bold;color:#000;text-decoration:none;overflow:hidden}nav .links a:after{display:block;height:1em;border-top:0.2em solid #ed1c24;content:"";position:relative;right:200%;bottom:.14em;transition:right 0.4s cubic-bezier(0, 0.5, 0, 1)}nav .links a:hover:after{right:0}#nav-toggle{position:absolute;top:-100px}nav .icon-burger{display:none;position:absolute;right:5%;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}nav .icon-burger .line{width:30px;height:5px;background-color:#000;margin:5px;border-radius:3px;transition:all .3s ease-in-out}@media screen and (max-width: 768px){nav .logo{float:none;width:auto;-webkit-justify-content:center;justify-content:center}nav .links{float:none;position:fixed;z-index:9;left:0;right:0;top:4rem;bottom:100%;width:auto;height:auto;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;background-color:rgba(0,0,0,0.8);overflow:hidden;box-sizing:border-box;transition:all .5s ease-in-out}nav .links a{font-size:20px;color:white}nav :checked ~ .links{bottom:0}nav .icon-burger{display:block}nav :checked ~ .icon-burger .line:nth-child(1){-webkit-transform:translateY(10px) rotate(225deg);transform:translateY(10px) rotate(225deg)}nav :checked ~ .icon-burger .line:nth-child(3){-webkit-transform:translateY(-10px) rotate(-225deg);transform:translateY(-10px) rotate(-225deg)}nav :checked ~ .icon-burger .line:nth-child(2){opacity:0}}

.titlePage{height:90vh;display:-webkit-flex;display:flex;-webkit-align-items:stretch;align-items:stretch;padding-bottom:0px;background-size:100vw;background-repeat:no-repeat;background-position-y:10vh}.titlePage::before{content:" ";position:absolute;margin-left:calc(-1 * (100vw - 960px) / 2);width:100vw;height:90vh;z-index:-1;background-image:url(/static/media/background.70f73be5.webp)}.titlePage-text{padding-top:15vh}.titlePage-text h2{font-size:3rem}.titlePage-text h3{font-size:2.5rem}.titlePage-text span{color:#ed1c24;font-weight:700}.titlePage-image{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.titlePage-image img{height:80vh;-webkit-align-self:flex-end;align-self:flex-end;padding-bottom:0}.titlePage p{padding:10px}@media screen and (max-width: 768px){.titlePage::before{display:none}.titlePage-image img{display:none}}

.about-description{display:-webkit-flex;display:flex;margin:3rem}.about-description h2{padding:0 1rem}.about-description p{padding:0 2rem;max-width:70ch}.about-skills{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%;vertical-align:center}.about-team{display:-webkit-flex;display:flex}.about-team>*{-webkit-flex-basis:100%;flex-basis:100%}@media screen and (max-width: 768px){.about-description{-webkit-flex-direction:column;flex-direction:column}.about-team{-webkit-flex-flow:row wrap;flex-flow:row wrap}}

.progress{width:100%}.progress-label{margin-bottom:.4em}.progress-content{box-sizing:content-box;height:20px;position:relative;background:#d2c9c9;border-radius:25px;padding:10px;box-shadow:inset 0 -1px 1px rgba(255,255,255,0.3)}.progress-content>span{display:block;height:100%;border-radius:20px 8px 8px 20px;background-color:#ed1c24;box-shadow:inset 0 2px 9px rgba(255,255,255,0.3),inset 0 -2px 6px rgba(0,0,0,0.4);position:relative;overflow:hidden}

.about-card{margin:0 1rem;padding:1rem;box-shadow:4px 4px 20px 0 rgba(0,0,0,0.75);border-radius:.4rem}.about-card-photo img{width:100%;height:auto}.about-card-description h4{margin:.7rem 0;font-weight:700;font-size:1.2rem}.about-card-description p{text-align:justify}@media screen and (max-width: 768px){.about-card{-webkit-flex:0 1 100%;flex:0 1 100%;max-width:300px;margin-top:1rem;margin-right:auto;margin-left:auto}}

.services{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.services p{max-width:100ch;font-weight:500;font-size:1.1rem;text-align:center}

.contactUs{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.contactUs-icon{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#ed1c24;padding:1rem;border-radius:50%;width:4rem;height:4rem}.contactUs-icon path{color:white}.contactUs a{text-decoration:none;color:#000;font-weight:700}

footer{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}img{padding:10px;height:5rem}

*,*::after,*::before{box-sizing:border-box}body{padding:0;margin:0;font-family:'Nunito', sans-serif;position:relative;font-weight:normal;font-style:normal;font-size:15px;line-height:1.7;color:#262821;overflow-x:hidden}.wrapper{margin-right:auto;margin-left:auto;max-width:960px;padding-right:10px;padding-left:10px}section{padding-bottom:5rem}h1{font-weight:800;position:relative;padding-left:15px;padding-right:20px;display:inline-block;margin-bottom:15px;font-size:2.75rem}h1::after{content:"";background:#ed1c24;border-radius:7px;width:5px;height:100%;position:absolute;left:0;top:0;z-index:-1}h1 span{color:#ed1c24}h1 span:before{content:' '}

